.section.atividade-objetivo-section{
  padding-top: 90px;
  padding-bottom: 90px;
  &.bg-blue {
    background-color: $blue;
  }
}

.atividade-item_subtitulo {
  margin-top: 80px;
  margin-bottom: 55px;
  h2 {
    font-size: 54px;
    margin: 0;
  }
}

.atividade-objetivo {
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  padding-right: 35%;
  @media (max-width: 991px) {
    padding-right: 0 !important;
  }
  .atividade-objetivo_conteudo {
    text-align: left;
    color: $white;
    h2 {
      font-size: 54px;
    }
    p {
      padding-left: 20px;
      position: relative;
      &::before {
        content: "";
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: $white;
        position: absolute;
        left: 0;
        top: 7px;
      }
    }
  }
  .atividade-objetivo_imagem {
    @media (max-width: 991px) {
      display: none;
    }
    width: 35%;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    > img {
      width: 100%;
      height: auto;
    }
  }
}

.atividade-item{
  position: relative;
  margin-bottom: 80px;
  &.atividade-item_left {
    padding-right: 25%;
    @media (max-width: 767px) {
      padding-right: 0 !important;
    }
  }
  &.atividade-item_right{
    padding-left: 25%;
    @media (max-width: 767px) {
      padding-left: 0 !important;
    }
  }
  .atividade-item_numero {
    margin-bottom: 20px;
    .inner {
      width: 100px;
      height: 100px;
      background-color: rgba($color: $blue, $alpha: .2);
      display: inline-block;
      border-radius: 50%;
      text-align: center;
      line-height: 98px;
      h3 {
        font-size: 64px;
        margin: 0;
      }
    }
  }
  .atividade-item_titulo{
    margin-bottom: 20px;
    h2 {
      font-size: 42px;
    }
  }
  .atividade-item_conteudo{
    position: relative;
    z-index: 2;
    strong {
      color: $blue;
    }
  }
  .atividade-item_tempo-proposto{
    p {
      margin: 0;
      font-weight: 500;
      color: $blue;
      padding-left: 50px;
      position: relative;
      line-height: 40px;
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgba($color: $blue, $alpha: .2);
        position: absolute;
        left: 0;
        top: 0;
        background-image: url('../images/icon-alarm.svg');
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }
  .atividade-item_imagem {
    position: absolute;
    width: 28%;
    right: 0;
    top: 0;
    @media (max-width: 767px) {
      display: none !important;
    }
  }
}

.atividade-video{
  padding: 35px;
  background-color: $blue;
  border-radius: 5px;
  margin-bottom: 60px;
  @media (max-width: 767px) {
    .infor {
      margin-top: 50px;
    }
  }
  .video {
    .embed-responsive {
      border-radius: 5px;
      .display {
        width: 100%;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba($color: $colortext, $alpha: .5); 
      }
      .embed-responsive-item {
        z-index: 9;
        background-position: center;
        background-size: cover;
        background-color: rgba($color: $blue, $alpha: .5); //
      }
      .icon-play {
        position: absolute;
        width: 72px;
        height: 72px;
        border-radius: 50%;
        z-index: 10;
        border: 2px solid $white;
        top: 50%;
        left: 50%;
        transform: scale(1) translate(-50%, -50%);
        background-image: url('../images/icon-play.png');
        background-position: center;
        background-repeat: no-repeat;
        transition:  all .25s ease-in-out;
      }
    }
  }
  .content{
    margin-top: 60px;
    h2 {
      font-size: 42px;
      color: $white;
    }
    p {
      color: $white;
    }
  }
  .infor {
    .atividade-video_duracao {
      margin-bottom: 20px;
      p {
        .icon {
          background-image: url('../images/icon-alarm-white.svg');
        }
      }
    }
    .atividade-video_informacoes {
      margin-bottom: 20px;
      p {
        .icon {
          background-image: url('../images/icon-infor-white.svg');
        }
      }
    }
    p {
      color: $white;
      margin-top: 0;
      margin: 0;
      padding-left: 50px;
      position: relative;
      font-size: 14px;
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgba($color: $white, $alpha: .25);
        position: absolute;
        left: 0;
        top: 0;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
  }
}

.atividade-donwload{
  .atividade-download_titulo{
    h2 {
      font-size: 68px;
    }
  }
  .atividade-download_arquivo {
    margin-top: 40px;
    p {
      margin: 0;
      font-weight: 700;
      color: $blue;
      padding-left: 50px;
      position: relative;
      line-height: 40px;
      a {
        text-decoration: none;
      }
      .icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: rgba($color: $blue, $alpha: .2);
        position: absolute;
        left: 0;
        top: 0;
        background-position: center;
        background-repeat: no-repeat;
        text-align: center;
        line-height: 50px;
        i {
          font-size: 24px;
        }
      }
    }
  }
}


.atividade-competncias{
  margin-top: 100px;
  margin-bottom: 50px;
  .atividade-competncias_titulo {
    h2 {
      font-size: 54px;
    }
  }
}

.atividade-referencia {
  padding-top: 100px;
  padding-bottom: 100px;
  .atividade-referencia_bibliografia {
    h2 {
      font-size: 48px;
      color: $white;
      margin-bottom: 15px;
    }
    p {
      color: $white;
      @media (max-width: 767px) {
        > br {
          display: none;
        }
      }
    }
  }
  .atividade-referencia_bibliografia{
    .btn-link {
      border: 2px solid $white;
      background-color: transparent;
      font-weight: 700;
      padding: 0 60px;
      &:hover {
        color: $white;
      }
    }
  }
}