.custom-list_title {
  margin-top: 100px;
  margin-bottom: 50px;
}

.custom-list_item {
  margin-bottom: 20px;
  .icone {
    width: 100px;
    height: 100px;
    display: inline-block;
    position: relative;
    > img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    > svg {
      width: 100%;
      height: auto;
    }
    &.icone-purple {
      > svg > path {
        fill: $purple;
      }
    }
    &.icone-orange {
      > svg > path {
        fill: $orange;
      }
    }
    &.icone-blue {
      > svg > path {
        fill: $blue;
      }
    }
    &.icone-yellow {
      > svg > path {
        fill: $yellow;
      }
    }
  }
  .text {
    p {
      margin-top: 0;
    }
  }
}