.wp-block {
  .container {
    max-width: 90%;
    margin: 0 5%;
  }
  .atividade-objetivo-section {
    position: relative;
    .triangle {
      position: absolute;
      left: -5%;
      right: -5%;
      display: inline-block;
      z-index: 9;
      > img {
        width: 110%;
        border-radius: 0;
        float: left;
        max-width: none;
      }
      &.triangle-top {
        top: - 1px;
      }
      &.triangle-bottom {
        bottom: - 1px;
      }
    }
  }
  .atividade-referencia {
    padding-top: 100px;
    padding-bottom: 100px;
    background-color: transparent;
  }
}